import { isEmptyArray } from './is.js';
let weakId = 0;
export const weakUnique = () => Math.random().toString(32).slice(2);
export const weakUniqueId = () => `${weakUnique()}${++weakId}`;
export const weakTimed = () => Date.now().toString(32);
export const weakTimedId = () => `${weakTimed()}${++weakId}`;
export const fastUnique = () => `${weakUnique()}${weakTimed()}`;
export const fastUniqueId = () => `${fastUnique()}${++weakId}`;
/** @nosideeffects */
export const findIndex = (arr, predicate) => {
    return arr.findIndex(predicate);
};
/** @nosideeffects */
export const findLastIndex = (arr, predicate) => {
    for (let i = arr.length; i--;) {
        if (predicate(arr[i])) {
            return i;
        }
    }
    return -1;
};
/** @nosideeffects */
export const find = (arr, predicate) => {
    return arr.find(predicate);
};
/** @nosideeffects */
export const findLast = (arr, predicate) => {
    const index = findLastIndex(arr, predicate);
    return index === -1 ? null : arr[index];
};
/** @nosideeffects */
export const last = (arr) => {
    return isEmptyArray(arr) ? null : arr[arr.length - 1];
};
